import React from "react"
import { Link, graphql } from "gatsby"
import Img from "gatsby-image"
import styled, { keyframes } from "styled-components"
import strip from "../images/strip.svg"
import PropTypes from "prop-types"
import CategoryList from "../components/category_list"
import {snakeCase} from 'lodash'

// smoll
const FImage = styled(Img)`

`
const topToBottom = keyframes`
  0% {
    opacity: 0;
    transform: translateY(150px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
`;

const PostContainer = styled.div`
  width: 30%;
  display: inline-block;
  opacity: 0;
  margin: 25px 10px;

  &:nth-child(2) {
    animation: ${topToBottom} 1s ease-out 0s forwards;
  }
  &:nth-child(3) {
    animation: ${topToBottom} 1s ease-out 0.2s forwards;
  }
  &:nth-child(4) {
    animation: ${topToBottom} 1s ease-out 0.4s forwards;
  }
  &:nth-child(5) {
    animation: ${topToBottom} 1s ease-out 0.4s forwards;
  }
  &:nth-child(6) {
    animation: ${topToBottom} 1s ease-out 0.6s forwards;
  }
  &:nth-child(7) {
    animation: ${topToBottom} 1s ease-out 0.8s forwards;
  }
  &:nth-child(8) {
    animation: ${topToBottom} 1s ease-out 0.8s forwards;
  }
  &:nth-child(9) {
    animation: ${topToBottom} 1s ease-out 1s forwards;
  }
  &:nth-child(10) {
    animation: ${topToBottom} 1s ease-out 1.2s forwards;
  }
 
`

const PostContent = styled.div`
  display: inline-block;
  p {
    font-size: small;
  }
  padding: 10px 5px;
`

const Card = styled.div`
  background-color: #fefefe;
  -webkit-box-shadow: 1px 4px 5px 0 rgba(0,0,0,0.26);
  -moz-box-shadow: 1px 4px 5px 0 rgba(0,0,0,0.26);
  box-shadow: 1px 4px 5px 0 rgba(0,0,0,0.26);
  text-align: left;
  min-width: 250px;
  transition: transform 0.2s ease-out;
  border: 1px #cecece solid;

  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    opacity: 0;
    -webkit-box-shadow: 5px 20px 25px rgba(0,0,0,.26);
    -moz-box-shadow: 5px 20px 25px rgba(0,0,0,.26);
    box-shadow: 5px 20px 25px rgba(0,0,0,.26);
    transition: opacity 0.2s ease-out;
  }

  &:hover{
    transform: translateY( -8px) scale(1.03);
    transition: transform 0.3s ease-out;
    &:after {
      opacity: 1;
      transition: opacity 0.3s ease-out;
    }
  }
`

const PostHeader = styled.div`
  font-size: smaller;
  color: #d05641;
`

const PostTitle = styled.h3`
  height: 40px;
`

const PostExcerpt = styled.p`
  height: 80px;
`

const Separator = styled.div`
  width:100%;
  height: 5px;
  background-color: #3c3c3c;
  background-image: url(${strip});
  background-repeat: no-repeat;
  background-position: right 20px center;
`

const Categories = styled(Link)`
    color:#6897ff;
    &:hover {
      color: #df4d55;
    }
`

class BlogIndex extends React.Component {
 static propTypes = {
    location: PropTypes.object.isRequired
  }
  render() {
    const { data } = this.props
    return (
        <div>

          <h4>{data.allMarkdownRemark.totalCount} Posts</h4>
            {data.allMarkdownRemark.edges.map(({ node }) => (
              <PostContainer key={node.id}>
                <Card>
                  <Link to={node.fields.path}>
                    {/* immediately-invoked function expressions to wrap the if */}
                    {(() => {
                      if (node.frontmatter.featuredImage) {
                        return <FImage fluid={node.frontmatter.featuredImage.childImageSharp.fluid}/>
                      } else {
                        return <FImage fluid={data.mainBg.childImageSharp.fluid}/>
                      }
                    })()}
                    <Separator />
                    <PostContent>
                      <PostHeader>{node.frontmatter.date} | By: {node.frontmatter.author}</PostHeader>
                      <PostTitle>
                        {node.frontmatter.title}
                      </PostTitle>
                      <PostExcerpt>{node.excerpt}</PostExcerpt>
                      <hr/>
                      <span>
                      {node.frontmatter.categories.map((tag, i) => {
                        return (
                          <Categories key={'tag-'+tag+'-'+i} to={`/category/${snakeCase(tag)}/`}>#{tag}{i < node.frontmatter.categories.length - 1 ? ", " : ""}</Categories>
                        )
                      })}
                    </span>
                    </PostContent>
                  </Link>
                </Card>
              </PostContainer>
            ))}
          <CategoryList />
        </div>
    )
  }
}


export default BlogIndex

export const query = graphql`
  query blogListQuery($skip: Int!, $limit: Int!) {
    site {
      siteMetadata {
        title
      }
    }
  
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      limit: $limit
      skip: $skip
    ) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "DD MMMM YYYY")
            author
            featuredImage {
              childImageSharp {
                fluid(maxWidth: 400) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
            categories
          }
          fields {
            slug
            path
          }
          excerpt
        }
      }
    }
    
     mainBg: file(relativePath: { eq: "placeholder.png" }) {
      childImageSharp {
        fluid(maxWidth: 800) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`